$orange: #FF800B;

.product__btn:active {
    position: relative;
    bottom: -1px;
}

.product__btn:hover {
    background-color: #ff922b;
    transition: all .3s ease 0s;
}


.header {
    padding: 20px 0;
    position: fixed;
    left: 0;
    z-index: 50;
    right: 0;
}

.header.active {
    backdrop-filter: blur(20px);
    background-color: #fbbd37e7;
}

.header__container {
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.header__logo {
    font-weight: 700;
    font-size: 36px;
    line-height: 42px;
    color: #233506;
}

.header__logo span {
    color: $orange;
}

.menu {
    justify-self: center;
    margin-left: 250px;
}
.menu__list {
    display:flex;
    align-items: center;
    column-gap: 46px;
    flex-wrap: wrap;
}
.menu__item {
    font-size: 18px;
    line-height: 21px;
    color: #233506;
    font-weight: 500;
    position: relative;
}

.menu__item::after {
    content: '';
    width: 0%;
    height: 2px;
    position: absolute;
    background-color: #233506;
    left: 0;
    bottom: -2px;
    transition: all .5s ease 0s;
}

.menu__item:hover::after {
    width: 100%;
}

.off {
    overflow: hidden;
}

.burger {
    display: none;
}

.header__btns {
    display: flex;
    align-items: center;
    column-gap: 13px;
    border-radius: 20px;
    flex-wrap: wrap;
}
.contact-us__btn {
    font-size: 18px;
    line-height: 21px;
    color: $orange;
    padding: 20px 23px;
    border-radius: 22px;
    background-color: #FFFFFF;
}
.basket__btn {
    position: relative;
    padding: 17px;
    border-radius: 15px;
    background-color: #FFFFFF;
}

.basket__plush {
    position: absolute;
    padding: 1px 4px 1px 4px;
    right: -3px;
    top: -3px;
    font-weight: 700;
    font-size: 18px;
    border-radius: 7px;   
    color: #F9F1E7;
    background-color: #ff0000;
    display: none; 
}

.basket__plush.active {
    display: block;
}

.search__btn {
    border-radius: 15px;
    position: relative;
    z-index: auto;
    padding: 17px;  
    background-color: #FFFFFF;
}

// ###################### ADAPTATION ##########################

@media (max-width: 1099px) {
    .menu {
        justify-self: center;
        margin-left: 100px;
        margin-right: 50px;
    }
}

@media (max-width: 858px) {
    .header-logo {
        margin-right: 20px;
    }

    .burger {
        position: relative;
        display: block;
        width: 30px;
        height: 20px;
        cursor: pointer;
        z-index: 101;
    }

    .burger span,
    .burger::before,
    .burger::after{
        position: absolute;
        height: 15%;
        width: 100%;
        background-color: #3A3A3A;
    }

    .burger::before,
    .burger::after {
        content: "";
    }

    .burger::before {
        top: 0;
    }

    .burger::after {
        bottom: 0;
    }

    .burger span {
        top: 50%;
        transform: translate(0, -50%);
        opacity: 1;
    }

    .menu {
        position: fixed;
        left: -100%;
        top: 0;
        width: 100%;
        height: 100%;
        background-color: #000000b4;
        overflow: auto;
        z-index: 100;
        margin: 0;
    }

    .menu.active {
        left: 0;
        transition: all .5s ease 0s;
    }

    .burger {
        order: 1;
    }

    .burger.active span,
    .burger.active::after,
    .burger.active::before{
        background-color: #F9F1E7;
        transition: background-color .5s ease 0s;
        transition: all .5s ease 0s;
    }

    .burger.active span {
        display: none;
    }

    .burger.active::after {
        bottom: 50%;
        transform: translate(0,50%) rotate(135deg);
    }

    .burger.active::before {
        top: 50%;
        transform: translate(0,-50%)  rotate(-135deg);
    }

    .menu__list {
        position: absolute;
        display: block;
        float: right;
        right: 5%;
        top: 80px;
        gap: 40px 15px;
    }

    .menu__item {
        display: block;
        font-size: 22px;
        color: #F9F1E7;
        margin-bottom: 20px;
    }

    .menu__item::after {
        background-color: #F9F1E7;
        transform: translate(0);
        left: 0;
    } 
    .header.active {
        backdrop-filter: none;
    }
}

@media (max-width: 449px) {
    .contact-us__btn {
        display: none;
    }
}