// ##################### PREV ##############################

.main {

}
.main__container {
}
.preview {
    position: relative;
    padding-top: 150px;
    background-image: url(../../img/prevBack.png);
    background-repeat: no-repeat; 
    background-size: 100%;
    background-position: center 10px;
    padding-bottom: 100px;
}

.preview::after {
    content: '';
