    font-weight: 400;
    font-size: 18px;
    line-height: 170%;
    color: rgba(35, 53, 6, 0.65);
    max-width: 456px;
}
.main__btn {
    margin-top: 60px;
    font-weight: 500;
    font-size: 18px;
    line-height: 18px;
    color: #FFFFFF;
    padding: 27px 40px;
    border-radius: 33px;
    background-color: #FDB827;
}

// ####################################### PRODUCTS ###################################

.products {
    background: #fff;
    margin-top: 100px;
    padding-bottom: 100px;
}
.products__container {
   display: flex;
   flex-direction: column;
}
.product__title {
    font-weight: 800;
    font-size: 70px;
    line-height: 82px;
    color: #233506;
    margin-bottom: 80px;
}
.product__cards {
    grid-template-columns: 1fr 1fr 1fr 1fr;
    display: grid;
    align-items: center;
    gap: 30px;
}

.product__card {
    max-width: 269px;
}

.product__card-image {
    max-width: 270px;
    max-height: 197px; 
    margin-bottom: 30px; 
}

.product__card__title {
    font-weight: 600;
    display: inline;
    font-size: 20px;
    line-height: 23px;
    color: #02283F;
}
.product__price {
    float: right;
    display: inline;
    font-weight: 600;
    font-size: 18px;
    line-height: 21px;
    color: #02283F;

}

.prod-inf {
    margin-top: 20px;
    align-items: center;
    display: flex;
    justify-content: space-between;
}

.product__size {
    font-weight: 600;
    font-size: 12px;
    line-height: 14px;
    color: rgba(2, 40, 63, 0.81);
}
.product__btn {
    font-family: 400;
    padding: 15px 37px;
    background-color: #FDB827;
    border-radius: 8px;
    font-size: 18px;
    line-height: 21px;
}

.basket-modal {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.759);
    opacity: 0;
    z-index: -3;
    transition: all .3s ease 0s;
}

.basket-modal.active {
    opacity: 1;
    z-index: 120;
}

.basket-modal__body{
    position: absolute;
    width: 320px;
    height: 100%;
    right: -100%;
    background-color: #ffffff;
    transition: all .5s ease 0s;
    overflow-y: auto;
    overflow-x: hidden;
}

.basket-modal__body.active {
    right: 0;
}

.basket-content {

}

.basket-no {
    position: absolute;
    text-align: center;
    left: 40%;
    top: 50%;
    transform: translate(-40%, -50%);
}
.basket-text {
    font-weight: 700;
    font-size: 36px;
}

.basket-cross {
    position: relative;
    display: block;
    width: 50px;
    height: 30px;
    cursor: pointer;
    z-index: 101;
    margin-top: 30px;
    margin-left: 30px;
}

.basket-cross::before,
.basket-cross::after{
    position: absolute;
    content: "";
    height: 15%;
    width: 100%;
    background-color: #3A3A3A;
}


.basket-cross::before {
    top: 50%;
    transform: translate(0, -50%) rotate(45deg);
}

.basket-cross::after {
    bottom: 50%;
    transform: translate(0, 50%) rotate(-45deg);
}
.basket-cart {
    margin-top: 20px;
    padding: 20px;
    border-radius: 20px;
    border: 3px solid #FDB827;
    width: 300px;
    margin-left: auto;
    margin-right: auto;
}
.basket-cart__body {
}
.basket-cart__img {
    border-radius: 20px;
}
.basket-cart__title {
    font-weight: 700;
    font-size: 23px;
    text-align: center;
    margin-top: 10px;
    margin-bottom: 5px;
    color: #FDB827;
}
.basket-cart__info {
    display: flex;
    align-items: center;
    justify-content: center;
    column-gap: 70px;
}
.basket-cart__price {
    font-weight: 700;
    font-size: 18px;
}
.basket-cart__size {
    font-size: 18px;
    font-weight: 700;
}

.basket-cart__btn {
    width: 100%;
    height: 40px;
    margin-top: 10px;
    font-size: 23px;
    color: #fff;
    border-radius: 20px;
    font-weight: 700;
    border: 2px solid #FDB827;
    background-color: #FDB827;
    transition: all .5s ease 0s;
}

.basket-cart__btn:hover {
    color: #FDB827;
    background-color: #fff;
}

