// ################################### SEARCH #########################

.search__modal {
    position: fixed;
    height: 0;
    width: 0;
    background-color: #0000009d;
    opacity: 0;
}

.search__modal.open {
    z-index: 200;
    height: 100vh;
    width: 100%;
    opacity: 1;
}

.search__container {
    position: relative;
}
.search__body {
    position: absolute;
    padding-top: 50px;
    background-color: #fc952eec;
    padding-bottom: 50px;
    width: 100%;
    left: 0;
    top: -100%;
}

.search__body.open {
    top: 0;
    transition: all .5s ease 0s;
}

.search__form {
    margin-bottom: 30px;
    text-align: center;
 
}
.search__input {
    width: 80%;
    padding: 10px;
    font-size: 22px;
    font-weight: 700;
    color: #fff;
    border: 2px solid #fff ;
}

.search__input::placeholder {
