}

.search-close {
    position: absolute;
    display: block;
    width: 50px;
    height: 30px;
    cursor: pointer;
    z-index: 101;
    right: 0;
    top: 10px;
}

.search-close::before,
.search-close::after{
    position: absolute;
    content: "";
    height: 15%;
    width: 100%;
    background-color: #fff;
}


.search-close::before {
    top: 50%;
    transform: translate(0, -50%) rotate(45deg);
}

.search-close::after {
    bottom: 50%;
