// ################################## ADAPTATION ########################

@media (max-width: 1099px) {
    .product__cards {
        grid-template-columns: 1fr 1fr 1fr;
    }
    .about__img {
        width: 500px;
        margin-left: 30px;
    }
    .review-swiper{
        width: 700px;
    }

    .swiper-wrapper{
        width: 700px;
    }

    .review-slide {
        border-radius: 68px;
        box-shadow: none;
        box-shadow: 0.5px 0.5px 5px 0px;
    } 
    .review-button-next {
        display: none;
    }
    .review-button-prev {
        display: none;
    }
}

@media (max-width: 899px) {
    .preview {
        position: relative;
        padding-top: 150px;
        background-image: url(../../img/prevBack.png);
        background-repeat: no-repeat; 
        background-size: 100%;
        background-position: center 150px;
        padding-bottom: 100px;
    }
    .work__images {
        display: none;
    }
    .about__img {
        width: 400px;
    }
}

@media (max-width: 799px) {
    .product__cards {
        grid-template-columns: 1fr 1fr;
        align-self: center;
    }
    .about__img {
        width: 350px;
    }
}

@media (max-width: 768px) {
    .preview {
        position: relative;
        padding-top: 150px;
        background-image: none;
        background-repeat: no-repeat; 
        background-size: 100%;
        background-position: center 150px;
        padding-bottom: 100px;
    }
    .main__title {
        font-size: 64px;
        margin-bottom: 30px;
    }

    .main__btn {
        margin-top: 30px;
        font-weight: 500;
        font-size: 18px;
        line-height: 18px;
        color: #FFFFFF;
        padding: 27px 40px;
        border-radius: 33px;
        background-color: #FDB827;
    }
    .about__img {
        width: 300px;
    }
} 

@media (max-width: 699px) {
    .about__img {
        display: none;
    }
    .about__text {
        margin-top: 30px;
    }
    .about__btn {
        margin-top: 30px;
    }
} 


@media (max-width: 519px) {
    .product__cards {
        grid-template-columns: 1fr;
    }
} 

@media (max-width: 499px) {
    .work__title {
        font-size: 60px;
        line-height: 72px;
    }
}


@media (max-width: 450px) {
    .main__title {
        font-size: 44px;
        margin-bottom: 30px;
    }
    .work__title {
        font-size: 50px;
        line-height: 62px;
    }
    .about__title {
        font-weight: 700;
        font-size: 60px;
        line-height: 72px;
    }
}

// ######################### ANIMATION #########################



@mixin baseAnim {
    transform: translateY(0);
    opacity: 1;
    transition: all .5s ease 0s;
}
.work__stage {
    transform: translate(20%,40%);
    opacity: 0;
}

.work__stage._active {
    @include baseAnim()
}

.about__title {
    transform: translateX(20%);
    opacity: 0;
}

.about__title._active {
    @include baseAnim()
}

.about__text {
    transform: translateX(20%);
    opacity: 0;
}

.about__text._active {
    @include baseAnim()
}

.about__btn {
    transform: translateX(20%);
    opacity: 0;
}

.about__btn._active {
    @include baseAnim()
}

@media (max-width: 1099px) {
    .work__stage {
        transform: translateY(40%);
        opacity: 0;
    }
    
    .work__stage._active {
        @include baseAnim()
    }
    
    .about__title {
        transform: translateY(40%);
        opacity: 0;
    }
    
    .about__title._active {
        @include baseAnim()
    }
    
    .about__text {
        transform: translateY(40%);
        opacity: 0;
    }
    
    .about__text._active {
        @include baseAnim()
    }
    
    .about__btn {
        transform: translateY(20%);
        opacity: 0;
    }
    
    .about__btn._active {
        @include baseAnim()
    }
}