.footer {
}

.up-footer {
    display: flex;
    justify-content: space-between;
    margin-bottom: 100px;
}

.footer__columns {
    display: flex;
    justify-content: space-between;
    flex: 1 1 auto;
    column-gap: 10px;
}
.main__column {
    margin-right: 150px;
}
.footer-main-column__title {
    font-family: 'Poppins';
    font-style: normal;
    font-weight: 600;
    font-size: 31px;
    color: #243165;
    margin-bottom: 47px;
}
.footer-column__text {
    max-width: 276px;
    font-weight: 400;
    font-size: 17px;
    line-height: 28px;
    display: flex;
    align-items: center;
    color: rgba(55, 84, 101, 0.8);
    margin-bottom: 37px;
}
.footer__icons {
    display: flex;
    align-items: center;
    column-gap: 20px;
}
.footer__icon {
}
.footer-column__title {
    font-family: 'Poppins';
    font-style: normal;
    font-weight: 600;
    font-size: 18px;
    color: #243165;
    margin-bottom: 45px;
}
.footer-column__item {
    font-weight: 400;
    font-size: 17px;
    line-height: 28px;
    display: flex;
    align-items: center;
    color: rgba(55, 84, 101, 0.8);
}

.down-footer {
    padding-top: 36px;
    padding-bottom: 42px;
    position: relative;
}

.down-footer::after {
    content: '';
    position: absolute;
    top: 0;
    width: 100%;
    height: 1px;
    background-color: #C4C4C4;
}

.down-footer__text {
    font-family: 'Poppins'; 
    font-weight: 400;
    font-size: 18px;
    text-align: center;
    line-height: 28px;
    color: #375465;
}

@media (max-width: 899px) {
    .main__column {
        margin-right: 50px;
    }
}

@media (max-width: 650px) {
    .main__column {
        margin-right: 15px;
    }
}

@media (max-width: 600px) {
    .footer__columns {
        flex-direction: column;
        justify-content: flex-start;
        row-gap: 20px;
    }
    .footer__columns {
        flex: 0 0 auto;
    }
    .footer-column__list {
        max-height: 0;  
        overflow: hidden;
        transition: all .5s ease 0s;
     
    }
    .footer-column__title {
        cursor: pointer;
        margin-bottom: 0px;
    }
}